import { SignupForm } from "@/components/SignupForm";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { SocialAuthButtons } from "@/components/auth/SocialAuthButtons";

export default function SignupPage() {
  return (
    <>
      <h2 className="mt-6 text-2xl font-bold">Create an account</h2>
      <p className="mt-2 text-muted-foreground">Start your journey with TeachLink today</p>
      
      <div className="space-y-6">
        <div>
           <SignupForm />
          <div className="w-full max-w-md px-4">
            <img 
              src="hero-image.jpeg" 
              alt="Woman learning with headphones and book" 
              className="w-full h-auto object-contain animate-float rounded-lg shadow-lg" 
            />
          </div>

        </div>
       
        
        <div className="relative">
          <div className="absolute inset-0 flex items-center">
            <span className="w-full border-t" />
          </div>
          <div className="relative flex justify-center text-xs uppercase">
            <span className="bg-background px-2 text-muted-foreground">Or continue with</span>
          </div>
        </div>
        
        <SocialAuthButtons />
        
        <div className="text-center text-sm">
          Already have an account?{" "}
          <Link href="/auth/login" className="text-primary hover:underline">
            Sign in
          </Link>
        </div>
      </div>
    </>
  );
} 